<template>
    <div >
        <div ref="editor" @blur="getContent" style="text-align:left" ></div>
        <!-- <button v-on:click="getContent"></button> -->
        <!-- <div ref="nimabi">{{editorContent}}</div> -->
    </div>
</template>

<script>
    import E from 'wangeditor'

    export default {
      name: 'editor',
      props:['data'],
      data () {
        return {
          editorContent: ''
        }
      },
      methods: {
        getContent: function () {
            // alert(this.editorContent)
            // console.log(this.editorContent)
            return this.editorContent;
        }
      },
      mounted() {
        var editor = new E(this.$refs.editor)

        editor.customConfig.onchange = (html) => {
          this.editorContent = html

        }
         editor.customConfig.uploadImgShowBase64 = true   //  base64 
        editor.create()
         editor.txt.html(this.data)
        

      }
    }
</script>

<style scoped>
</style>



