<template>
  <div  id="notice">
  	<section v-if="pageShow">
      <span class="title">{{$t('navBar.commonProblems')}}</span>
      <el-col :span="24" style="padding-bottom: 0px" class="toolbar">
        <el-form :inline="true" :model="userForm">
        	<el-form-item :label="$t('title')">
				    <el-input v-model="userForm.title"></el-input>
        	</el-form-item>
        	<!-- <el-form-item :label="$t('author')">
				    <el-input v-model="userForm.author"></el-input>
        	</el-form-item> -->
          <el-form-item>
            <el-button class="warning" @click="newJian">{{$t('AddAQuestion')}}</el-button>
          </el-form-item>
        </el-form>
      </el-col>
      <el-table :data="userList"   v-loading="userLoading" style="width: 100%;" :row-class-name="tableRowClassName" >
        <el-table-column prop="id"  :label="$t('QuestionSerialNo')">
        </el-table-column>
        <el-table-column prop="title" :label="$t('QuestionTitle')">
        </el-table-column>
        <el-table-column prop="author" :label="$t('notice.thead[4]')">
        </el-table-column>
        <el-table-column :label="$t('notice.thead[5]')">
          <template slot-scope="scope">
            <el-button size="mini"  @click="updateNotice(scope.$index,scope.row)">{{$t('xiuGai')}}</el-button>
            <!-- <el-button  size="mini"  @click="deleteNotice(scope.$index,scope.row)">{{$t('delete')}}</el-button> -->
            <el-button type="text" size="mini" @click="open2(scope.$index,scope.row)">{{$t('delete')}}</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="page" v-show="total >10">
        <el-col :span="24" class="toolbar  ">
          <el-pagination  background layout="prev, pager, next" :total="total" @current-change="handleCurrentChange">
          </el-pagination>
        </el-col>
      </div>
    </section>
    <section v-else class="tellNotice">
      <div><el-button @click="back">{{$t('back')}}</el-button></div> 
      <span class="title">{{$t('InputtingProblem')}}</span>
		<el-col :span="24" style="padding-bottom: 0px" class="toolbar">
	        <el-form :inline="true" :model="userForm">
		      	<el-form-item :label="$t('title')">
						    <el-input v-model="userForm.title"></el-input>
				</el-form-item>
			</el-form>
        </el-col>
      <Editor ref="editorSon" :data="publishDate.problemContent" class="editor"></Editor>
      <button v-if="publishDate.problemContent !=''" @click="xiuGaiNotice">{{$t('editIssue')}}</button>
      <button v-else @click="publishProblem">{{$t('InputtingProblem')}}</button>
    </section>
  </div>
</template>
<script>
import Editor from '@/components/editor'
import {ajax,dateStr2GmtTimeStamp,getDataYear} from '../../common/js/common.js'

export default {
  name:"problem",
   components:{
    Editor,
    },
   data(){
     return{ 
      pageShow:"true",
      checked:"",
       userForm:{
   		 number:"",
         startTime:'',
         endTime:'',
         enterpriseName:''
       },
       userLoading:false,
       userList:[],
       problemDate:{
        page:1,
        rowNum:10
       },
       total:null,
       publishDate:{
        title:"",
        problemContent:"",
       },
       updateData:{
        contentId:"",
        title:"",
        problemContent:"",

       }
     }
   },
   methods:{
   	back:function(){
   		this.pageShow=true;
   		this.userForm.title= "";
   	},
    queryProblems:function(){
      ajax(this,this.ApiConfig.problem.queryProblems,this.problemDate,(res) =>{
        if(res.code == 200){
          console.log(res)
          this.userList = res.data.rows;
          this.total = res.data.total;
        }else{
          this.$notify({
            title:'',
            message:res.message,
            type:'warning'
          })
          return false;
        }

        
      },"POST")
    },
    newJian:function(){
      this.pageShow=false;
      this.publishDate.problemContent = "";
      this.updateData. problemContent="";

    },
    // 
    publishProblem:function(){
      this.publishDate.title = this.userForm.title;
      this.publishDate.problemContent = this.$refs.editorSon.getContent();
      
      ajax(this,this.ApiConfig.problem.publishProblem,this.publishDate,(res) =>{
        if(res.code ==200){
          // console.log(res);
          this.pageShow=true;
          this.queryProblems();

          this.userForm.title="";
          this.userForm.author="";
        }else{
          this.$notify({
            title:'',
            message:res.message,
            type:'warning'
          })
          return false;
        }

        
      },"POST")
    },
     open2:function(index,row) {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.deleteNotice(index,row);
          this.$message({
            type: 'success',
            message: '!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });          
        });
      },
    // 
    deleteNotice:function(index,row){
      let para = {
        id:row.id
      }
      ajax(this,this.ApiConfig.problem.deleteProblem,para,(res) => {
        if(res.code == 200){
          // console.log(res)
          this.queryProblems();
        }else{
          this.$notify({
            title:'',
            message:res.message,
            type:'warning'
          })
          return false;
        }
        
      },"POST")
    },

    //
    updateNotice:function(index,row){
            console.log(row)
      //
      ajax(this,this.ApiConfig.problem.queryProblemContent,{contentId:row.id},(res) =>{
        if(res.code ==200){
             console.log(res)
             this.publishDate.problemContent = res.data.content;
             this.updateData.contentId=row.id,
             this.updateData. title=row.title,
             this.updateData. problemContent= this.publishDate.problemContent,
             this.pageShow=false;
             this.userForm.number = row.id;
             this.userForm.title = row.title;
        }else{
          this.$notify({
            title:'',
            message:res.message,
            type:'warning'
          })
          return false;
        }
      },'POST')
    },
     // 
      xiuGaiNotice:function(){
          this.updateData.title = this.userForm.title;
          if(this.$refs.editorSon.getContent()==""){
            this.updateData.problemContent = this.publishDate.problemContent
          }else{
              this.updateData. problemContent=this.$refs.editorSon.getContent() 
          }
          
          ajax(this,this.ApiConfig.problem.updateProblem,this.updateData,(res) =>{
            if(res.code == 200){
              console.log(res)
              this.pageShow=true;
              this.queryProblems();
              this.updateData. problemContent="";

            }else{
              this.$notify({
                title:'',
                message:res.message,
                type:'warning'
              })
              return false;
            }
              
          },"POST");
      },
    // 
    test:function(index,row,column){
     console.log(row)
     console.log(index)
     console.log(column.label)
     let data = {
      id:"",
      type:"",
      status:""
     }
     data.id = row.id;
       if(column.label ==""){
        data.type = 1;
          if(row.top ==1){
            
            data.status =0;
          }else{
            data.status =1
          }
       }else{
        data.type = 2;
        if(row.show ==1){
          data.status =0;
        }else{
          data.status =1;
        }
       }
     console.log(data)
     ajax(this,this.ApiConfig.notice.updateNoticeStatus,data,(res) =>{
      if(res.code == 200){
         console.log(res)
        this.queryProblems();
      }else{
        this.$notify({
          title:'',
          message:res.message,
          type:'warning'
        })
        return false;
      }
     
     },"POST")

    },
     getUsers:function(){

     },
     handleReset:function(){

     },
     tableRowClassName:function({row, rowIndex}){
         if(rowIndex % 2 == 1){
            return "success-row"
         }
     },
      handleCurrentChange:function(val){
       this.problemDate.page = val;
       this.queryProblems();
     },
   },
   mounted:function(){
    this.queryProblems();
   }
}
</script>

<style scoped lang="postcss">
.tellNotice{ 
  p{
  height: 20px;
  font-size: 14px;
  color: #000;
  padding-bottom: 20px;
}
  button{
    margin-top: 20px;
    width: 120px;
    height: 30px;
    background-color: #bf9764;
    border-radius: 5px;
    border:none;
    font-size: 14px;
    color: #ffffff;
  }
  
}
.editor{
  margin-top: 30px;
}
</style>
<style>
  #notice{
    .el-checkbox__input.is-checked .el-checkbox__inner, .el-checkbox__input.is-indeterminate .el-checkbox__inner{
      background-color: #bf9764;
      border-color: #bf9764;
    }
    .el-checkbox__input.is-checked+.el-checkbox__label{
      color:#bf9764 ;
    }
    .toolbar .el-form-item .warning{
      /* width: 85px; */
    }
    .w-e-toolbar{
      height: 40px;
      line-height: 32px;
      background-color: #e9ebf1!important;
    }
}
</style>






